import React, { FC } from 'react';
import { ColumnsProps, DefaultSortProps, FilterType, getExtraData, OrderType, SearchDataGrid, SearchViewProps, WindowComponent } from 'summer';
import { CLASS_NAME, ConfigIcmsDifal } from '../../../../models/dtos/mlf/configIcmsDifal';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'descricao',
    order: OrderType.ASC,
  },
];

const ConfigIcmsDifalSearchView: FC<SearchViewProps<ConfigIcmsDifal>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    {
      dataField: 'diaVencimento',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Dia de Vencimento',
    },
    {
      dataField: 'recolhimentoMinimo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Recolhimento Mínimo',
    },
  ];
  return (
    <WindowComponent title="Configurações de ICMS">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'ConfigPisEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default ConfigIcmsDifalSearchView;
